import { implement } from "@orpc/server";
import { Effect } from "effect";
import { z } from "zod";
/**
 * Common error schemas that all plugins can use
 * These provide consistent error handling across the plugin ecosystem
 */
export const CommonPluginErrors = {
    UNAUTHORIZED: {
        data: z.object({
            apiKeyProvided: z.boolean(),
            provider: z.string().optional(),
            authType: z.enum(['apiKey', 'oauth', 'token']).optional(),
        })
    },
    RATE_LIMITED: {
        data: z.object({
            retryAfter: z.number().int().min(1),
            remainingRequests: z.number().int().min(0).optional(),
            resetTime: z.string().datetime().optional(),
            limitType: z.enum(['requests', 'tokens', 'bandwidth']).optional(),
        })
    },
    SERVICE_UNAVAILABLE: {
        data: z.object({
            retryAfter: z.number().int().optional(),
            maintenanceWindow: z.boolean().default(false),
            estimatedUptime: z.string().datetime().optional(),
        })
    },
    BAD_REQUEST: {
        data: z.object({
            invalidFields: z.array(z.string()).optional(),
            validationErrors: z.array(z.object({
                field: z.string(),
                message: z.string(),
                code: z.string().optional(),
            })).optional(),
        })
    },
    NOT_FOUND: {
        data: z.object({
            resource: z.string().optional(),
            resourceId: z.string().optional(),
        })
    },
    FORBIDDEN: {
        data: z.object({
            requiredPermissions: z.array(z.string()).optional(),
            action: z.string().optional(),
        })
    }
};
/**
 * Factory function to create plugins
 */
export function createPlugin(config) {
    const configSchema = {
        variables: config.variables,
        secrets: config.secrets
    };
    class CreatedPlugin {
        id = config.id;
        contract = config.contract;
        configSchema = configSchema;
        _context = null;
        initialize(pluginConfig) {
            const init = config.initialize ?? (() => Effect.succeed({}));
            return init(pluginConfig).pipe(Effect.tap((ctx) => Effect.sync(() => { this._context = ctx; })), Effect.map(() => this._context), Effect.mapError((error) => error));
        }
        shutdown() {
            const self = this;
            return Effect.gen(function* () {
                if (config.shutdown && self._context) {
                    yield* config.shutdown(self._context).pipe(Effect.catchAll(() => Effect.void));
                }
            });
        }
        createRouter(context) {
            const builder = implement(config.contract).$context();
            const router = config.createRouter(context, builder);
            return router;
        }
    }
    const PluginConstructor = CreatedPlugin;
    PluginConstructor.binding = {
        contract: config.contract,
        config: configSchema
    };
    return PluginConstructor;
}
